class RocketComponent extends React.Component {
	render() {
		const rockets = this.props.rockets;
		const rocketsArr = [];
		for (let key in rockets){
			rocketsArr.push(rockets[key]);
		};
		console.log(rocketsArr);

		return (
			<section className="section">
				<div className="container">
					<div className="columns">
						{rocketsArr.map(rocket => (
							<Rocket
								id = {rocket.id}
								name = {rocket.name}
							/>
						))}
					</div>
				</div>
			</section>
		);
	};
};

function Rocket(props) {
	return (
		<div className="column" key={this.props.id}>
			<div className="notification c-hover">
				<p>a</p>
				{this.props.name}
			</div>
		</div>
	);
};

export default RocketComponent;